@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap");

* {
  box-sizing: border-box;
}

html {
  height: 100%;
}

#app {
  height: 100%;
}

body {
  font-family: "Raleway", sans-serif;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Raleway";
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol,
li {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
  font-family: "Raleway";
}

input {
  border: none;
  outline: none;
  transition: 0.3s;
  font-family: "Raleway";
}

button {
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s;
  font-family: "Raleway";
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
}
