@import "../../styles/variables.scss";

.button__main {
  border-radius: 4px;
  font-weight: 400;
  font-size: 13px;
  text-transform: uppercase;
  height: 50px;
  padding: 15px 10px;

  &_blue {
    border: 1px solid $white;
    background-color: $blue;
    color: $white;
    width: 210px;
    &:hover {
      border: 1px solid $blue;
      background-color: $white;
      color: $blue;
    }
  }
  &_white {
    border: 1px solid $btn-gray;
    background-color: $white;
    color: $btn-black;
    width: 210px;
    &:hover {
      border: 1px solid $blue;
      background-color: $white;
      color: $blue;
    }
  }
  &_blue-long {
    border: 1px solid $white;
    background-color: $blue;
    color: $white;
    width: 370px;
    &:hover {
      border: 1px solid $blue;
      background-color: $white;
      color: $blue;
    }
    @media screen and (max-width: 1200px) {
      width: 230px;
      margin: 0 auto;
    }
  }
}
